@font-face {
  font-family: "Roboto";
  font-style: normal;
  font-weight: 400;
  font-display: swap;
  src: local("Roboto Regular"), local("Roboto-Regular"), url("../fonts/Roboto-Regular.woff2") format("woff2"), url("../fonts/Roboto-Regular.woff") format("woff"); }

body {
  margin: 0; }

img {
  max-width: 100%; }

a {
  text-decoration: none; }

html {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  scroll-behavior: smooth; }

*,
*::before,
*::after {
  -webkit-box-sizing: inherit;
          box-sizing: inherit; }

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  -webkit-clip-path: inset(100%);
          clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden; }

.page {
  background-color: #ffffff;
  font-family: "Roboto", "Arial", sans-serif;
  color: #000000;
  font-weight: 400;
  margin: 0 auto;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  min-height: 100vh; }

.page__header,
.page__footer {
  background-color: #c4c4c4; }

.page__header h2,
.page__footer h2 {
  font-size: 24px;
  line-height: 20px;
  text-align: center;
  margin: 0;
  padding-top: 35px;
  padding-bottom: 35px; }

.page__main {
  -webkit-box-flex: 1;
      -ms-flex-positive: 1;
          flex-grow: 1; }

.page__footer {
  -ms-flex-negative: 0;
      flex-shrink: 0; }

.first-block {
  display: grid;
  gap: 10px;
  width: 100%;
  height: 100%; }

.first-block__tile {
  background-color: #a2a9b7; }

.first-block__tile p {
  margin: 0; }

.first-block__tile--first {
  grid-column: 1;
  grid-row: 1 / 3; }

.first-block__tile--second {
  grid-column: 2;
  grid-row: 1 / 1; }

.first-block__tile--third {
  grid-column: 2;
  grid-row: 2 / 2; }
